.wrapper {
  max-width: 80%;
  margin: 0 auto;
  color: #e5e5e5;
  text-align: justify;
  letter-spacing: 1px;
}

.section__header {
  font-family: "Whisper", cursive;
  font-weight: 400;
  font-style: normal;
  font-size: 50px;
  background-color: #111;
}

.section__tripleColumn {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
}

.section__images {
  max-width: 100%;
  border-radius: 5px;
}

.section__content {
  padding: 20px;
}

.section__images {  
  width: 100%; /* Zależnie od potrzeb, dostosuj szerokość */
  height: auto; /* Zachowuje proporcje obrazu */
  object-fit: cover;
  cursor: pointer;
  animation: pulse 800ms ease-in-out;
  filter: url(#noise); /* Filtr SVG */
}

.section__images:hover {
  filter: url(#noise); /* Wzmacniamy efekt na hover */
}

@keyframes pulse {
  from, to {
    scale: 1;
  }
  50% {
    scale: 1.08;
  }
}
